.home-container {
  max-width: 65%;
  margin: 0 auto;
  padding: 60px 20px;
  text-align: justify;
  color: #333;
  background: #f8f8f8;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  border-radius: 15px;
}

.home-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
  padding: 30px;
  background-color: #fff;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.home-title-container {
  max-width: 60%;
}

.home-image {
  max-width: 35%;
  border-radius: 10px;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.1);
}

.home-title {
  font-size: 35px;
  font-weight: 600;
  color: #2f4858;
  margin-bottom: 20px;
}

.home-subtitle {
  font-size: 20px;
  color: #4f6367;
}

.home-section {
  margin-bottom: 60px;
  padding: 30px;
  background-color: #fff;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
}

.home-section-title {
  font-size: 30px;
  font-weight: 500;
  color: #2f4858;
  margin-bottom: 30px;
}

.home-section-list {
  list-style: none;
  padding: 0;
  margin: 0;
  line-height: 1.6;
}

.home-section-list li {
  margin-bottom: 15px;
  font-size: 18px;
  color: #4f6367;
  position: relative;
}

.home-section-list li::before {
  content: "âž¤";
  font-size: 18px;
  position: absolute;
  left: -25px;
  color: #2f4858;
}

.home-section-text {
  font-size: 18px;
  line-height: 1.6;
  color: #4f6367;
}

/* Media Queries */

@media (max-width: 768px) {
  .home-container {
    padding: 40px 10px;
  }

  .home-title {
    font-size: 28px;
  }

  .home-subtitle {
    font-size: 18px;
  }
}

@media (max-width: 576px) {
  .home-container {
    max-width: 95%;
    padding: 30px 10px;
  }

  .home-header {
    flex-direction: column;
  }

  .home-title-container,
  .home-image {
    max-width: 100%;
    margin-bottom: 15px;
  }

  .home-title {
    font-size: 24px;
  }

  .home-subtitle {
    font-size: 16px;
  }

  .home-section-title {
    font-size: 26px;
  }

  .home-section-list li {
    font-size: 16px;
  }

  .home-section-text {
    font-size: 16px;
  }

  .home-section-list li::before {
    left: -20px;
  }
}
